.container {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	width: 100%;
	min-height: 100vh;
}

.content_container {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-ms-flex-direction: column;
	flex-direction: column;
	width: 100%;
	max-width: 960px;
}

/* 
Stretches the available area below the navigation bar to fill the available screen height.
*/
.inner_content_container {
	height: 100%;
}

.navigation_bar {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
	-webkit-box-pack: end;
	-ms-flex-pack: end;
	justify-content: flex-end;
}

.nav_button {
	padding: 1em;
	cursor: pointer;
}

@-webkit-keyframes fadeIn {
	from {
		opacity: 0;
		-webkit-transform: translateX(-1%);
		transform: translateX(-1%);
	}
	to {
		opacity: 1;
		-webkit-transform: translateX(1);
		transform: translateX(1);
	}
}

@keyframes fadeIn {
	from {
		opacity: 0;
		-webkit-transform: translateX(-1%);
		transform: translateX(-1%);
	}
	to {
		opacity: 1;
		-webkit-transform: translateX(1);
		transform: translateX(1);
	}
}

.fadeIn {
	-webkit-animation-duration: var(--transition-duration);
	animation-duration: var(--transition-duration);
	-webkit-animation-fill-mode: forwards;
	animation-fill-mode: forwards;
	opacity: 0;
	-webkit-transform: translateX(1);
	-ms-transform: translateX(1);
	transform: translateX(1);
	-webkit-animation-name: fadeIn;
	animation-name: fadeIn;
	-webkit-animation-timing-function: ease-in-out;
	animation-timing-function: ease-in-out;
}

@media only screen and (max-width: 600px) {
	.column_container {
		grid-template-columns: repeat(8, 1fr);
	}

	.colored_column_container {
		-ms-grid-column: 2;
		-ms-grid-column-span: 2;
		grid-column: 2 / 4;
	}

	.navigation_bar {
		-webkit-box-pack: center;
		-ms-flex-pack: center;
		justify-content: center;
	}

	.nav_item {
		-webkit-box-flex: 1;
		-ms-flex-positive: 1;
		flex-grow: 1;
		-ms-flex-preferred-size: 20%;
		flex-basis: 20%;
	}
}

.accent_background_container {
	position: fixed;
	top: 0%;
	left: 0%;
	height: 100%;
	width: 100%;
}

.accent_background_container::after {
	content: '';
	height: 100px;
	width: 100px;
	border-radius: 50%;
	position: fixed;
	background: -o-linear-gradient(
		290deg,
		var(--color-easyBlack) 0%,
		var(--color-mainAccent) 100%
	);
	background: linear-gradient(
		160deg,
		var(--color-easyBlack) 0%,
		var(--color-mainAccent) 100%
	);
	z-index: -50;
	top: 50%;
	left: 10%;
}
